html {
  line-height: 1.15;
  -webkit-text-size-adjust: 100%;
}
body {
  margin: 0;
}
main {
  display: block;
}
h1 {
  font-size: 2em;
  margin: 0.67em 0;
}
hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible;
}
pre {
  font-family: monospace, monospace;
  font-size: 1em;
}
a {
  background-color: transparent;
}
abbr[title] {
  border-bottom: none;
  text-decoration: underline;
  text-decoration: underline dotted;
}
b,
strong {
  font-weight: bolder;
}
code,
kbd,
samp {
  font-family: monospace, monospace;
  font-size: 1em;
}
small {
  font-size: 80%;
}
sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}
sub {
  bottom: -0.25em;
}
sup {
  top: -0.5em;
}
img {
  border-style: none;
}
button,
input,
optgroup,
select,
textarea {
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
  margin: 0;
}
button,
input {
  overflow: visible;
}
button,
select {
  text-transform: none;
}
button,
[type="button"],
[type="reset"],
[type="submit"] {
  -webkit-appearance: button;
}
button::-moz-focus-inner,
[type="button"]::-moz-focus-inner,
[type="reset"]::-moz-focus-inner,
[type="submit"]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}
button:-moz-focusring,
[type="button"]:-moz-focusring,
[type="reset"]:-moz-focusring,
[type="submit"]:-moz-focusring {
  outline: 1px dotted ButtonText;
}
fieldset {
  padding: 0.35em 0.75em 0.625em;
}
legend {
  box-sizing: border-box;
  color: inherit;
  display: table;
  max-width: 100%;
  padding: 0;
  /* 3 */
  white-space: normal;
}
progress {
  vertical-align: baseline;
}
textarea {
  overflow: auto;
}
[type="checkbox"],
[type="radio"] {
  box-sizing: border-box;
  padding: 0;
}
[type="number"]::-webkit-inner-spin-button,
[type="number"]::-webkit-outer-spin-button {
  height: auto;
}
[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}
[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}
::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}
details {
  display: block;
}
summary {
  display: list-item;
}
template {
  display: none;
}
[hidden] {
  display: none;
}
@font-face {
  font-family: "Dosis";
  src: url("../fonts/dosis.woff2"), url("../fonts/dosis.woff");
  font-weight: normal;
  font-style: normal;
}
* {
  box-sizing: border-box;
}
.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
html {
  scroll-behavior: smooth;
}
body {
  background-color: #fff;
  font-family: "Dosis", "Arial", sans-serif;
  font-size: 17px;
  font-weight: 400;
  min-width: 320px;
  padding: 0;
  margin: 0;
}
a {
  outline: none;
  text-decoration: none;
  color: black;
}
ul {
  margin: 0;
}
.container {
  width: 1250px;
  margin: 0px auto;
  padding: 0px 20px;
}
.page-header {
  height: 1065px;
  background: url(../img/raster/png/header-logo.png) no-repeat center 220px, url(../img/raster/png/header-background.png) no-repeat center;
}
.page-header__top {
  display: flex;
  align-items: center;
  width: 100%;
  background-color: rgba(0, 8, 12, 0.1);
  border-top: 2px solid #d9dddd;
  height: 60px;
}
.navigation__list {
  list-style: none;
  display: flex;
  justify-content: space-between;
  padding: 0;
}
.navigation__link {
  position: relative;
  text-transform: uppercase;
  color: #606060;
  letter-spacing: 2px;
}
.navigation__link:hover,
.navigation__link:focus {
  color: #000;
}
.navigation__link::before {
  width: 0;
  content: "";
  position: absolute;
  bottom: -10px;
  left: 50%;
  border-bottom: 2px solid #000;
  -webkit-transition: 0.3s;
  transition: 0.3s;
  opacity: 0;
}
.navigation__link:hover::before,
.navigation__link:focus::before {
  width: 100%;
  left: 0;
  -webkit-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
  opacity: 1;
}
.discography {
  padding: 55px 0 455px;
  background: url('../img/raster/png/discography-backgroung.png') no-repeat bottom;
  background-size: contain;
}
.discography__title {
  color: #996b4f;
}
.discography__description {
  width: 720px;
  margin: 35px auto 0;
  font-size: 14px;
  text-align: center;
  letter-spacing: 3px;
  line-height: 25px;
  color: #9da3a6;
}
.discography__albums-list {
  list-style: none;
  padding-left: 0;
  margin-top: 130px;
}
.discography__albums-item {
  display: flex;
  justify-content: space-between;
}
.discography__album-name-block {
  width: 505px;
  padding-top: 35px;
  font-size: 15px;
}
.discography__album-description-block {
  width: 680px;
}
.discography__year {
  font-weight: bold;
  color: #595959;
}
.discography__album-title {
  margin: 8px 0;
  padding-top: 7px;
  border-top: 1px solid #000;
  text-transform: uppercase;
}
.discography__title-description-block {
  font-size: 25px;
  margin: 15px 0;
  letter-spacing: 1px;
}
.discography__album-description {
  line-height: 25px;
  color: #acacac;
}
.discography__play-link {
  display: flex;
  width: 195px;
  height: 55px;
  text-transform: uppercase;
  font-weight: bold;
  justify-content: center;
  align-items: center;
  margin-top: 40px;
  margin-bottom: 110px;
}
.first-play-link {
  background: url('../img/raster/png/discography-button1.png') no-repeat;
  color: #fff;
}
.second-play-link {
  background: url('../img/raster/png/discography-button2.png') no-repeat;
}
.third-play-link {
  background: url('../img/raster/png/discography-button3.png') no-repeat;
}
.topic-title {
  position: relative;
  width: max-content;
  font-size: 50px;
  letter-spacing: 6px;
  text-transform: uppercase;
  font-weight: normal;
  margin: auto;
}
.topic-title::before {
  content: '';
  width: 145%;
  height: 1px;
  position: absolute;
  top: 50%;
  left: -22%;
  z-index: -1;
  background-color: #000;
}
.concert-tours__title {
  background: -webkit-linear-gradient(#62a2ad, #8dbab3);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.concert-tours {
  padding: 165px 0 250px;
  background: url('../img/raster/png/concert-tours-background.png') no-repeat bottom;
  background-size: contain;
}
.concert-tours__description {
  width: 720px;
  margin: 35px auto 470px;
  font-size: 14px;
  text-align: center;
  letter-spacing: 3px;
  line-height: 25px;
  color: #9da3a6;
}
.concert-tours__list {
  list-style: none;
  width: 680px;
}
.concert-tours__date {
  color: #000;
}
.concert-tours__name {
  margin: 8px 0;
  padding-top: 7px;
  border-top: 1px solid #000;
  text-transform: uppercase;
}
.concert-tours__play-link {
  width: 195px;
  height: 55px;
  border: 1px solid #b5b7b7;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  font-weight: bold;
}
.concert-tours__play-link svg {
  width: 10px;
  margin-top: 3px;
  transform: rotate(-90deg);
}
.concert-tours__album-description {
  line-height: 25px;
}
.latter-compositions {
  padding: 164px 0 160px;
}
.latter-compositions__description {
  width: 515px;
  margin: 35px auto 45px;
  font-size: 14px;
  text-align: center;
  letter-spacing: 2px;
  line-height: 20px;
  color: #9da3a6;
}
.latter-compositions__description-block,
.latter-compositions__player-block {
  width: 600px;
}
.latter-compositions__item {
  display: flex;
  margin-bottom: 55px;
}
.latter-compositions__name {
  margin: 10px 0 40px;
  padding-top: 7px;
  border-top: 1px solid #000;
  text-transform: uppercase;
}
.latter-compositions__play-link {
  width: 195px;
  height: 55px;
  border: 1px solid #b5b7b7;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  font-weight: 700;
}
.latter-compositions__play-link svg {
  width: 10px;
  margin-top: 3px;
  transform: rotate(-90deg);
}
.latter-compositions__title-description-block {
  margin-bottom: 35px;
}
.latter-compositions__player-block {
  background: url("../img/raster/png/player-first-track.png") no-repeat;
}
.latter-compositions__description-block {
  background: #f5f6f6;
  padding: 33px;
}
.second-player {
  background: url("../img/raster/png/player-second-track.png") no-repeat;
}
.third-player {
  background: url("../img/raster/png/player-third-track.png") no-repeat;
}
.slider-block {
  height: 645px;
  background: url("../img/raster/png/slider-background.png") no-repeat;
  background-size: cover;
  position: relative;
  overflow: hidden;
}
.slider-block::before,
.slider-block::after {
  content: '';
  display: block;
  width: 170px;
  height: 170px;
  background: #fff;
  border-radius: 5px;
  position: absolute;
  top: calc(50% - 85px);
  transform: rotate(45deg);
}
.slider-block::before {
  left: -85px;
}
.slider-block::after {
  right: -85px;
}
.slider-block__arrow {
  position: absolute;
  top: calc(50% - 12px);
  z-index: 1;
}
.prev-arrow {
  left: 40px;
  transform: rotate(90deg);
}
.next-arrow {
  right: 40px;
  transform: rotate(-90deg);
}
.slider-block__slider-name {
  width: 945px;
  color: #fff;
  font-size: 19px;
  text-transform: uppercase;
  letter-spacing: 3px;
  text-align: center;
}
.slider-block__slider-desc {
  width: 520px;
  color: #798285;
}
.slider-block__slider-signature {
  color: #565e61;
}
.slider-block__first-slider {
  display: flex;
  flex-direction: column;
  align-items: center;
}
